.App-BC {
    text-align: center;
    background-image: url("./images/Background2.png");
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    justify-content: center;
}

.From-Best {
  position: absolute;
    bottom: 100px;
    left: 40px;
    background-color:rgb(251, 254, 255);
    border-radius: 25px;
}


.From-div {
  
    width: 25vw;
    height: 55vh;
    border-radius: 25px;
    border-style: solid;
    box-shadow: 1px 1px 1px 1px grey;
    border-color: black;
    padding-top: 40px;
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 30px;
  }
  
  @media only screen and (max-width: 768px) {
    .From-Best {
        border-radius: 25px;
        width: 91vw;
        
    }
  
    .From-div {
    width: 90vw;
    height: 65vh;
      padding-top: 10px;
      padding-right: 0px;
      padding-left: 0px;
      padding-bottom: 5px;
    }
  
    .From-Best {
      left: 10px;
      right: 10px;
    }
  
  }

  